import { Box, Grid, Typography } from '@mui/material'
import { getDownloadURL, getStorage, ref, uploadBytes } from 'firebase/storage'
import { useState } from 'react'
import { UploadArea } from './uploadFile/UploadArea'

export default function Upload({
  success,
  label,
  imgEx = "/images/id_card.gif",
  healperText = '*ถ่ายรูปให้ชัดเจน และไม่มีแสงสะท้อน',
  objectFit = 'cover'
}: {
  label: string
  success: (url: string) => void,
  imgEx?: string,
  healperText?: string,
  objectFit?: 'fill' | 'contain' | 'cover' | 'none' | 'scale-down'
}) {
  // const hiddenFileInput = useRef(null)
  const [filePath, setFilePath] = useState<string>('')

  const handleChange = async (fileData: File) => {
    const storage = getStorage()
    const storageRef = ref(storage, `images/${fileData.name}`)

    const snapshot = await uploadBytes(storageRef, fileData)
    const data = await getDownloadURL(snapshot.ref)
    setFilePath(data)
    success(data)
  }

  // const handleClick = () => {
  //   const current = hiddenFileInput.current as any
  //   current.click()
  // }

  return (
    <Box marginBottom={3}>
      <Grid container alignItems='center' spacing={2}>
        <Grid item sm={12} md={6}>
          <Typography variant="subtitle1">{label}</Typography>

          <UploadArea onChange={handleChange} previewPath={filePath} />
        </Grid>
        <Grid item sm={12} md={6}>
          <Typography variant="subtitle1">ตัวอย่าง</Typography>
          <Typography variant='caption' color='rebeccapurple'>{healperText}</Typography>
          <img style={{ borderRadius: '4px', height: '181px', objectFit }} width='100%' src={imgEx} />
          {/* {filePath && <img style={{ borderRadius: '4px' }} width='100%' src={filePath} />} */}
        </Grid>
      </Grid>
    </Box>
  )
}
