import { PictureAsPdf, UploadFile } from '@mui/icons-material'
import { Box, Typography } from '@mui/material'
import { Fragment, useState } from 'react'
import { FileUploader } from 'react-drag-drop-files'

const fileTypes = ['PDF', 'jpg', 'JPEG', 'png', 'gif']

export const UploadArea = ({
  onChange
}: {
  onChange: (data: File) => void
  previewPath?: string
}) => {
  const [fileType, setFileType] = useState<{
    file: File | null
    name: string
    type: string
  }>({ file: null, name: '', type: '' })

  const handleChange = async (file: File) => {
    if (file) {
      onChange(file)
      setFileType({ file, name: file.name, type: file.type.split('/')[1] })
    }
  }
  return (
    <Fragment>
      <FileUploader
        hoverTitle={''}
        handleChange={handleChange}
        name="file"
        types={fileTypes}
      >
        <Box
          width="100%"
          height="200px"
          border="3px dashed #c3c3c3"
          display="flex"
          justifyContent="center"
          alignItems="center"
          borderRadius="4px"
          style={{ cursor: 'pointer' }}
        >
          <Box
            textAlign="center"
            height="100%"
            width="100%"
            display="flex"
            alignItems="center"
            justifyContent="center"
          >
            {!!fileType.file && (
              <Fragment>
                {fileType.type === 'pdf' ? (
                  <Box textAlign="center">
                    <PictureAsPdf fontSize="large" />
                    <Typography>{fileType.name}</Typography>
                  </Box>
                ) : (
                  <img
                    style={{ borderRadius: '4px', objectFit: 'cover' }}
                    width="100%"
                    height="100%"
                    src={URL.createObjectURL(fileType.file)}
                  />
                )}
              </Fragment>
            )}
            {!!!fileType.file && (
              <Box>
                <Typography variant="body2" color="InactiveCaptionText">
                  คลิก หรือ ลาก และวาง อัพโหลด
                </Typography>
                <UploadFile color="disabled" fontSize="large" />
              </Box>
            )}
            {/* <Typography variant="body2" color='InactiveCaptionText'>รองรับไฟล์ {["xlsx", "csv", 'PDF', 'jpg', 'JPEG', 'png', 'gif'].join(', ')}</Typography> */}
          </Box>
        </Box>
      </FileUploader>
    </Fragment>
  )
}
